/* Brand colors */
body {
  position: relative;
  background: white;
}

section {
  padding-top: 0;
}

.margin-top-reset {
  margin-top: none !important;
}

#documentation {
  display: block;
  position: relative;
  overflow-x: hidden;
}

#documentation #content {
  width: inherit;
  display: block;
  position: relative;
  padding-left: 200px;
}

@media (max-width: 767px) {
  #documentation #content {
    padding-left: 0;
  }
}

#documentation #sidebar {
  border-right: 1px solid #e3e3e3;
  display: block;
  position: fixed;
  top: 90px;
  left: 0px;
  padding-bottom: 15px;
  bottom: 0;
  width: 200px;
  z-index: 1;
}

@media (max-width: 767px) {
  #documentation #sidebar {
    width: 100%;
    z-index: 2;
    text-align: center;
    background: #fff;
    font-size: 1rem;
    visibility: hidden;
    transform: translateX(-100%);
    transition: all 0.5s ease;
  }
  #documentation #sidebar.sidebar-visible {
    visibility: visible;
    transform: translateX(0%);
  }
}

#sidebar-toggle {
  display: none;
  position: fixed;
  right: 1rem;
  top: 120px;
  padding: 1rem;
  line-height: 0.5rem;
  text-align: center;
  cursor: pointer;
  z-index: 999;
  background: #fff;
  border: 1px solid;
  transition: background 0.3s ease;
}

@media (max-width: 767px) {
  #sidebar-toggle {
    display: block;
  }
}

#sidebar-toggle:hover, #sidebar-toggle:focus, #sidebar-toggle:active {
  background: #f2f2f2;
}

ul.table-of-contents {
  list-style: none;
  padding: 0;
  margin: 20px 0 0 20px;
  font-size: 14px;
}

ul.table-of-contents > li {
  margin-bottom: 5px;
}

ul.table-of-contents > li.active ul {
  display: block;
}

ul.table-of-contents > li.active > a {
  color: #1A1A1A;
  border-left: 2px solid #E32546;
}

ul.table-of-contents > li > a {
  color: #aaa;
  border-left: 2px solid transparent;
  transition: all 0.5s ease;
  padding-left: 20px;
}

ul.table-of-contents > li > a:hover {
  color: #1A1A1A;
  border-left: 2px solid #E32546;
}

ul.table-of-contents > li ul {
  display: none;
  padding: 0;
  list-style: none;
}

ul.table-of-contents > li ul li {
  margin-top: 2px;
}

ul.table-of-contents > li ul li a {
  color: #aaa;
  padding: 0 0 0 30px;
  border-left: 1px solid #f2f2f2;
}

ul.table-of-contents > li ul li.active a {
  border-left-color: #E32546;
  color: #1A1A1A;
}

ul.table-of-contents > li.table-heading {
  padding-left: 0px;
  margin: 0 0 10px;
}

#footer-copyright {
  z-index: 2;
  position: relative;
}

#social-section {
  z-index: 1;
}

.documentation-heading {
  margin-bottom: 2rem;
}

.documentation-description {
  font-size: 1.2rem;
  color: #686d71;
}

.documentation-title {
  font-size: 4.5rem;
  font-weight: 300;
}

.documentation-subtitle {
  font-size: 2rem;
  font-weight: 300;
  margin: 3rem 0 1rem;
}
